<template>
  <div id="index">
    <div class="aside"></div>
    <div class="content">
      <div>
        <div class="title">我制定的计划</div>
        <el-row :gutter="32">
          <el-col :span="6">
            <div class="grid-content"  @click="onViewClick">
              <div class="content-head clear">
                <span class="left">标题</span>
                <div class="right">
                  <span>编辑</span>
                  <span>星标</span>
                </div>
              </div>
            </div>
          </el-col>
          <el-col :span="6"><div class="grid-content"></div></el-col>
          <el-col :span="6"><div class="grid-content"></div></el-col>
          <el-col :span="6">
            <div class="grid-content create" @click="onShowCreateDiglogClick">
              <i class="iconfont icon-jiahao btn"></i>
              <span class="text">创建新项目</span>
            </div>
          </el-col>
        </el-row>
      </div>
      <div>
        <div class="title">
          <span>项目回收站</span>
          <span class="show-hide" @click="recycleVisible=!recycleVisible">
            <span v-if="!recycleVisible">显示</span>
            <span v-else>隐藏</span>
          </span>
        </div>
        <div v-if="recycleVisible">
          <el-row :gutter="32">
            <el-col :span="6">
              <div class="grid-content">
                <div class="content-head clear">
                  <span class="left">标题</span>
                  <div class="right">
                    <span>编辑</span>
                    <span>星标</span>
                  </div>
                </div>
              </div>
            </el-col>
            <el-col :span="6"><div class="grid-content"></div></el-col>
            <el-col :span="6"><div class="grid-content"></div></el-col>
          </el-row>
        </div>
        
      </div>
    </div>
    <div class="aside"></div>


    <el-dialog
      title="提示"
      :visible.sync="createDialogVisible"
      width="400px">
      <div slot="title" class="create-dialog-title">创建项目</div>
      <div class="dialog-content-wrap">
        <div class="pic"></div>
        
      </div>
      <span slot="footer">
        <el-button type="primary" style="width:100%;" @click="createDialogVisible = false">完成并创建</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      createDialogVisible: false,
      recycleVisible: false
    }
  },
  methods: {
    onViewClick() {
      this.$router.push("/view");
    },
    onShowCreateDiglogClick() {
      this.createDialogVisible = true;
    }
  }
}
</script>

<style lang="scss">
  #index {
    width: 100%;
    height: 100%;
    display: flex;

    .content {
      flex: 3;
      margin-top: 50px;
      .content-head {
        height: 30px;
        line-height: 30px;
        padding: 8px 16px;
      }
      .el-row {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .el-col {
        border-radius: 4px;
      }
      .grid-content {
        position: relative;
        background-image: url(https://mailimg.teambition.com/logos/cover-media.jpg);
        border-radius: 4px;
        min-height: 128px;
        margin-bottom: 20px;
        bottom: 0;
        box-shadow: 0 0 0 rgba(56,56,56,.15);
        transition: bottom,box-shadow 0.3s,0.2s;
        -webkit-transition: bottom,box-shadow 0.3s,0.2s;
        &:hover {
          cursor:pointer;
          bottom: 8px;
          box-shadow: 2px 8px 10px rgba(0,0,0,0.15);
        }
      }

      .create {
        background-image: none;
        background-color: #fff;
        line-height: 96px;
        color: #A6A6A6;
        &:hover {
          color: #3EA8F5;
        }
        .btn {
          font-size: 40px;
          height: 36px;
          display: block;
          text-align: center;
          margin-bottom: 6px;
        }
        .text {
          height: 18px;
          font-size: 18px;
          display: block;
          text-align: center;
        }
      }
    }
    .aside {
      flex: 1;
    }
  }

  .title {
    height: 20px;
    line-height: 20px;
    font-size: 18px;
    margin-bottom: 16px;

    .show-hide {
      font-size: 15px;
      color: #A6A6A6;
      &:hover {
        color: #3EA8F5;
      }
    }
  }

  .create-dialog-title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #E5E5E5;
    
  }

  .dialog-content-wrap {
    .pic {
      background-image: url(https://dn-st.teambition.net/teambition/images/illustration@3x.2d8d2cc3.png);
      background-size: 113px 90px;
      width: 113px;
      height: 90px;
      margin: 0px auto;
    }
  }


</style>



