<template>
  <div id="app">
    <el-container>
      <el-header>
        <div class="nav">
          <header class="nav-header clear">
            <i class="iconfont icon-caidan"></i>
            <span>个人项目</span>
          </header>
          <section class="nav-section"></section>
          <footer class="nav-footer clear">
            <div class="nav-footer-wrap">
              <i class="iconfont icon-wenhao"></i>
              <span class="calendar">日历</span>
              <i class="iconfont icon-tongzhi"></i>
              <i class="iconfont icon-liuyan"></i>
              <span>名字</span>
            </div>
          </footer>
        </div>
      </el-header>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="scss">
  @import './style/common';
  @import './style/font/iconfont.css';
  #app {
    height: 100%;
  }
  .el-container {
    height: 100%;
  }
  .el-header {
    background-color: #FFF;
    height: 48px;
    box-shadow: 0 1px 3px 0 rgba(0,0,0,0.15);
    padding: 0 0;
    .nav {
      display: flex;
      height: 100%;
      .nav-header {
        width: 124px;
        padding-left: 10px;
        &:hover {
          cursor:pointer;
        }
        i {
          display: inline-block;
          color: #A6A6A6;
          font-size: 26px;
          margin: 12px 6px 0 0;
          float: left;
          &:hover {
            color: #3EA8F5;
          }
        }
        span {
          display: inline-block;
          font-size: 18px;
          float: left;
          margin-top: 13px;
        }
      }
      .nav-section {
        flex: 1;
      }
      .nav-footer {
        width: 324px;
        .nav-footer-wrap {
          float: right;
          margin-right: 20px;
        }
        .calendar {
          padding: 0 10px 0 10px;
          text-align: center;
          font-size: 16px;
          display: inline-block;
          width: 40px;
          height: 24px;
          border-left: 1px solid #A6A6A6;
          border-right: 1px solid #A6A6A6;
          margin-right: 20px;
          &:hover {
            color: #3EA8F5;
            cursor:pointer;
          }
        }
        i {
          color: #A6A6A6;
          font-size: 26px;
          margin-right: 20px;
          margin-top: 10px;
          &:hover {
            color: #3EA8F5;
            cursor:pointer;
          }
          float: left;
        }
        span {
          margin-top: 13px;
          float: left;
        }
      }

    }
  }
  
  .el-main {
    height: 100%;
    background-color: #F5F5F5;
    padding: 0 0;
  }
</style>



